.potassium {
  width: 100%;
  margin-top: 2rem;
}
.potassium .title {
  flex-direction: column;
  align-items: center;
}
.potassium .title h3 {
  width: 100%;
  padding: 0.7rem;
  text-align: center;
  color: #fff;
  background-color: #a35d5d;
  margin-bottom: 1.5rem;
}
@media (min-width: 768px) {
  .potassium .title h3 {
    border-radius: 0.5rem;
    padding: 1rem;
  }
}
.potassium .title p {
  font-size: 1.6rem;
  color: #a35d5d;
}
.potassium .title span {
  font-weight: bold;
  color: #000;
}
.potassium .list {
  width: 100%;
  padding: 0 3rem;
}
.potassium .list h4 {
  font-size: 1.7rem;
  margin: 1rem 0;
}
.potassium .list p,
.potassium .list li {
  margin-left: 2rem;
  font-size: 1.6rem;
}
.potassium .list ul {
  margin: 1rem;
}